import { graphql } from "gatsby";
import * as React from "react";

import Layout from "../layout/layout";

import "swiper/swiper.scss";
import Swiper from "react-id-swiper";
import styled from "styled-components";

export const HeadingStyle = styled.section`
    width: 1200px;
    margin: 0 auto;
    position: relative;
    margin-top: 150px;
    height: 400px;
    span {
        display: block;
    }
    .tit {
        font-size: 54px;
        color: #444;
        letter-spacing: -7px;
        font-weight: 300;
        margin-left: 500px;

        span {
            display: block;
            margin-top: -15px;

            &.lined:after {
                content: "";
                position: absolute;
                top: 194px;
                left: 235px;
                width: 250px;
                height: 3px;
                background: #444;
            }
        }
    }
    .desc {
        position: absolute;
        left: 279px;
        top: 223px;
        text-align: right;
        color: #a7a7a7;
        font-size: 12px;
        font-weight: 100;
    }
`;
export const LinkStyle = styled.section`
    width: 1200px;
    margin: 0 auto;
    text-align: left;
    box-sizing: border-box;
    padding-left: 500px;
    .button {
        border: 1px solid #ddd;
        padding: 5px 15px;
        display: inline-block;
        font-size: 11px;
        margin-right: 10px;
        border-radius: 3px;
        transition: all 0.3s ease;
        margin-top: 10px;
        background: #fff;
        min-width: 70px;
        text-align: center;
    }
`;
export const InfoStyle = styled.section`
    width: 1200px;
    margin: 0 auto;
    position: relative;
    margin-top: 0;
    .desc {
        color: #989191;
        font-size: 12px;
        font-weight: 300;
        padding-top: 0;
        margin-left: 100px;
        width: 600px;
        padding: 50px 0;
        margin: 0 auto;
        padding-left: 400px;
        span {
            display: block;
        }
    }
`;
export const SkinImgStyle = styled.div`
    position: relative;
    height: 4350px;
    .skin_img {
        transition: all 0.5s ease-in-out;
        cursor: pointer;

        .img1 {
            position: absolute;
            top: 0;
            left: 100px;
            z-index: 4;
        }

        .img2 {
            position: absolute;
            top: 1300px;
            left: 100px;
            z-index: 3;
        }

        .img3 {
            position: absolute;
            top: 2100px;
            left: 100px;
            z-index: 2;
        }

        .img4 {
            position: absolute;
            top: 3500px;
            left: 0;
            z-index: 1;
        }
    }
`;
export const SkinselfStyle = styled.section`
    position: relative;
    width: 100%;
    margin: 50px 0;
    .position: relative;
    width: 1200px;
    margin: 0 auto;
    overflow: hidden;
    .wrap {
        position: relative;
        width: 1200px;
        margin: 0 auto;
        overflow: hidden;
        .thumb {
            &.left {
                position: relative;
                float: left;
            }

            &.right {
                position: relative;
                float: right;
            }
        }
        .desc {
            width: 300px;
            margin-top: 70px;
            h2 {
                position: relative;
                font-size: 36px;
                color: #333;
                padding-bottom: 50px;
                font-weight: normal;

                &:after {
                    content: "";
                    position: absolute;
                    top: 80px;
                    left: 0;
                    height: 3px;
                    width: 100px;
                    background: #333;
                }
            }
            p {
                font-size: 13px;
                color: #aaa;
            }
        }
        .left {
            position: absolute;
            top: 0;
            left: 0;
            &50 {
                left: 50px;
            }
            &100 {
                left: 100px;
            }
            &150 {
                left: 150px;
            }
        }
        .right {
            position: absolute;
            top: 0;
            right: 0;
            &50 {
                right: 50px;
            }
            &100 {
                right: 100px;
            }
            &150 {
                right: 150px;
            }
        }
        .top100 {
            top: 100px;
        }
    }
`;
export const TxtDescStyle = styled.section`
    width: 100%;
    margin: 0 auto;
    position: relative;
    margin-top: 300px;
    margin-bottom: 300px;
    text-align: center;
    overflow: hidden;

    &:before {
        content: "";
        position: absolute;
        top: 31%;
        width: 800px;
        height: 1px;
        background: #eee;
        left: -500px;
    }
    &:after {
        content: "";
        position: absolute;
        top: 31%;
        width: 800px;
        height: 1px;
        background: #eee;
        right: -500px;
    }
    span {
        display: block;
        text-align: center;
        font-size: 13px;
        color: #7b7a7a;
    }
    .button {
        border: 1px solid #ddd;
        padding: 5px 15px;
        display: inline-block;
        font-size: 11px;
        margin-right: 10px;
        border-radius: 3px;
        transition: all 0.3s ease;
        margin-top: 10px;
        background: #fff;
        min-width: 70px;
        text-align: center;
    }
`;
export const DeskStyle = styled.section`
    position: relative;
    height: 879px;
    overflow: hidden;
    .link_list_upper {
        position: absolute;
        top: 113px;
        left: 50%;
        width: 150px;
        margin-left: 500px;

        .button {
            border: 1px solid #ddd;
            padding: 5px 15px;
            display: inline-block;
            font-size: 11px;
            margin-right: 10px;
            border-radius: 3px;
            transition: all 0.3s ease;
            margin-top: 10px;
            background: #fff;
            min-width: 70px;
            text-align: center;
        }
    }
`;

