import React, { ReactElement } from "react";
import { Link } from "gatsby";
import styled from "styled-components";

const FooterStyle = styled.footer`
    text-align: center;
    color: #ddd;
    font-size: 10px;
    color: #333;
    height: 90px;
    a {
        text-align: center;
        color: #333;
        font-weight: bold;
    }
`;
const MailStyle = styled.span`
    display: block;
    padding-top: 30px;
    font-size: 10px;
    color: #333;
`;
const HostStyle = styled.span`
    text-align: center;
    color: #ddd;
    font-size: 10px;
    color: #333;
    height: 90px;
`;

function Footer({}): ReactElement {
    return (
        <div>
            <FooterStyle>
                <MailStyle>
                    © ALL RIGHTS RESERVED{" "}
                    <a href="ishaiin@naver.com">ISHAIIN</a>.
                </MailStyle>
                <HostStyle>
                    POWERED BY{" "}
                    <a href="https://www.gatsbyjs.org/" target="_blank">
                        GATSBY
                    </a>
                    .
                </HostStyle>
            </FooterStyle>
        </div>
    );
}

export default Footer;
