import React, { ReactElement } from "react";
import { Link, graphql, useStaticQuery } from "gatsby";
import styled from "styled-components";

const HeaderStyle = styled.header`
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    min-width: 1200px;
    height: 53px;
    z-index: 30;
    background: rgba(255, 255, 255, 0.8);
    h1 {
        position: absolute;
        top: 15px;
        left: 25px;
        margin-top: 0;
        font-size: 15px;
        font-weight: 500;
        color: #444;
        a {
            font-size: 17px;
            font-weight: 500;
            color: #444;
            img {
                width: 30px;
            }
        }
    }
`;

const NavStyle = styled.nav`
    position: absolute;
    top: 0;
    left: 50%;
    font-size: 12px;
    font-weight: 400;
    color: #333;
    ul {
        overflow: hidden;
        margin-left: -135px;
        li {
            float: left;
            list-style: none;
            margin-left: 30px;

            &:first-child {
                margin-left: 0;
            }
            a {
                padding-top: 20px;
                color: #444;
                text-decoration: none;
                position: relative;
                padding-bottom: 20px;
                display: block;

                &:after {
                    content: "";
                    position: absolute;
                    top: 28px;
                    left: 0%;
                    border-bottom: 1px solid #333;
                    width: 0%;
                    height: 1px;
                    opacity: 0;
                    -webkit-transition: all 0.1s ease-in-out;
                    transition: all 0.1s ease-in-out;
                }

                &:hover {
                    &:after {
                        width: 100%;
                        top: 28px;
                        opacity: 1;
                    }
                }
            }
        }
    }
`;

const SinceStyle = styled.p`
    position: absolute;
    top: 15px;
    right: 25px;
    margin-top: 0;
    font-size: 12px;
    font-weight: bold;
    color: #444;
    font-family: "Roboto Slab";
`;

function Header() {
    return (
        <>
            <HeaderStyle>
                <h1 className="logo">
                    <Link id="home" className="nav-link" to="/">
                        <img src={`./s2/images/dot.png`} alt="drop" />
                    </Link>
                </h1>
                <NavStyle>
                    <ul>
                        <li>
                            <Link id="skin3" className="nav-link" to="/skin3">
                                가작3
                            </Link>
                        </li>
                        <li>
                            <Link id="skin3" className="nav-link" to="/skin5">
                                가작5
                            </Link>
                        </li>
                        <li>
                            <Link id="skin3" className="nav-link" to="/skin6">
                                가작6
                            </Link>
                        </li>
                        <li>
                            <Link id="skin3" className="nav-link" to="/skin10">
                                가작10
                            </Link>
                        </li>
                    </ul>
                </NavStyle>
                <SinceStyle>SINCE 2014.05</SinceStyle>
            </HeaderStyle>
        </>
    );
}

export default Header;
